import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import BinService from '../services/BinService';
const url = 'https://hook-mock.herokuapp.com';

const binService = new BinService({ url });

const App = () => (
  <Router>
    <div>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/list">List all Bins</Link>
        </li>
      </ul>

      <hr />

      <Route exact path="/" component={Home} />
      <Route exact path="/bin/:hash" component={Results} />
    </div>
  </Router>
);


class Results extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      hash: props.match.params.hash,
      bins: [],
      total: null,
      binUrl: `${url}/bin/${props.match.params.hash}`
    };
  }

  componentDidMount(){
    this.loadResults();
  }

  loadResults() {
    binService.getByHash(this.state.hash)
      .then((result) => {
        console.log(result.bins.total)
        this.setState({
          bins: result.bins,
          total: result.bins.length
        });
      });
  }
  
  renderRequest(request, index) {
    return (
      <li key={index}>
        <p>Time: {request.created_at}</p>
        <p>Method: {request.method}</p>
        
        <p>Headers:</p>
        <ul>
          {
            Object.entries(request.headers)
              .map(([key, value], index) => (
                <li key={index}>
                  <b>{key}:</b> {value}
                </li>
              ))
          }
        </ul>
        
        <p>Body:</p>
        <code>{ JSON.stringify(request.body, null, 2) }</code>
        
        <p>Query:</p>
        <ul>
          {
            Object.entries(request.query)
              .map(([key, value], index) => (
                <li key={index}>
                  <b>{key}:</b> {value}
                </li>
              ))
          }
        </ul>
      </li>
    )
  }

  render() {
    return (
      <div>
        <h2>Results</h2>
        <h3>Total: {this.state.total}</h3>
        <h3>Url: {this.state.binUrl}</h3>
        <ul>
          { this.state.bins.reverse().map(this.renderRequest) }
        </ul>
      </div>
    );
  }
}

class Home extends Component {
  constructor(params) {
    super(params);
    this.state = {};
  }

  createBin() {
    binService.generateHash()
      .then((result) => {
        this.props.history.push(`/bin/${result.hash}`)
      });
  }

  render() {
    return (
      <div>
        <h2>Home</h2>
        <button onClick={this.createBin.bind(this)}>Create Bin</button>
      </div>
    );
  }
}


export default App;


